* {
  margin: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.headerOption {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 20px;
    color: gray;
    cursor: pointer;

}

.headerOption__title {
    font-size: 12px; 
    font-weight: 400;
}

.headerOption:hover {
    color:black;
}
.NavBar {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    display: flex;
    justify-content: space-between;
    padding-top: 10px;
    padding-bottom: 10px;
    width: 100%;
    z-index: 999;
    background-color: white;
    
    
    }
    
    .NavBar__left {
        display: flex;
        font-size: 30px;
        color: rgb(126, 135, 127);
        
    }
    

    
    .NavBar__right {
        display: flex; 
    
    }
    
    
.feed {
    display: flex; 
    overflow-x: scroll ;
    overflow-y: scroll;


}

img {
    display: flex;
    height: 600px;
    width: 350px;
    border-radius: 12px;


}

iframe {
    display: flex;
    height: 600px;
    width: 350px;
    background-color: none;
    border-radius: 12px;



}
.Feedstyle {
    display: flex;
    height: 700px;
    width: 100%;
    margin-top: 20px;

  

  

}

.Feedc {
    display: flex; 
    align-items: center;
    align-content: center;
    justify-content: center;
    margin-left: 20px;
    margin-right: 20px;
    width: 350px;
    height: 600px;


border-radius: 12px;

box-shadow:
2.8px 2.8px 2.2px rgba(0, 0, 0, 0.02),
6.7px 6.7px 5.3px rgba(0, 0, 0, 0.028),
