22.3px 22.3px 17.9px rgba(0, 0, 0, 0.042),
41.8px 41.8px 33.4px rgba(0, 0, 0, 0.05),
100px 100px 80px rgba(0, 0, 0, 0.07)
;
    




   
}


.techfeed {
    display: flex; 
    overflow-y: hidden;
    overflow-x: scroll;
	width: 100%;

    


}
	
	




.techimage {
    display: flex;
    height: 100px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
   
  ;
   


}

iframe {
    display: flex;
    height: 600px;
    width: 350px;
    background-color: none;
    border-radius: 12px;



}
.TechFeedstyle {
    display: flex;
    height: 400px;
    width: 100%;


   
}

.TechFeedc {
    display: flex; 
    align-items: flex-start;
